<template>
    <div class="documents">
        <h1>Мои документы</h1>
        <table>
            <thead>
                <tr>
                    <th>Название</th>
                    <th class="invisible">&nbsp;</th>
                    <th class="invisible">&nbsp;</th>
                    <th class="invisible">&nbsp;</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="i in list" :key="i.id">
                    <td>{{ i.name }}</td>
                    <td class="invisible"><button class="delete" @click="deleteDocument(i.id)">Удалить документ</button></td>
                    <td class="invisible"><button class="btn" @click="showDocument(i.id)">Открыть документ</button></td>
                    <td class="invisible"><button class="green" @click="downloadDocument(i.id, i.name)">Скачать документ</button></td>
                </tr>
            </tbody>
        </table>
        <button class="delete" @click="exit">Выйти</button>
    </div> 
</template>
<script>
import Lib from '@/Library';
export default {
    data() {
        return {
            api: new Lib.Api('http://localhost:8000', localStorage.token),
            list: [],
            docId: 0,
            doc: '',
        }
    },
    mounted() {
        this.update()
    },
    methods: {
        deleteDocument(id) {
            (async (id) => {
                this.api.deleteDoc(id);
                this.list = this.api.list();
            })(id)
        },
        showDocument(id) {
            (async (id) => {
                let document = await this.api.one(id);
                localStorage.elementsData = Lib.xmlToFrontData(document);
            })(id)
        },
        downloadDocument(id, name){
            (async (id, name) => {
                let document = await this.api.one(id);
                Lib.downloadDocumentFromXML(document, name);
            })(id, name)
        },
        exit() {
            localStorage.clear();
            window.location.href = './login';
        },
        update(){
            (async () => {
                this.list = await this.api.list();
            })();
        }
    }
}
</script>

<style scoped>
.documents input {
    min-width: 33%;
    width: fit-content;
    border: transparent;
    padding: 1px 3px;
    margin: 5px 10px;
    border-bottom: black solid 1px;
    transition: .4s;
    font-size: 16px;
}

pre {
    text-align: left;
    width: fit-content;
    margin: 0 auto;
}

.documents {
    width: 1280px;
    margin: 0 auto;
    margin-top: 40px;
}

table {
    border-collapse: collapse;
    width: 100%;
    margin-top: 30px;
}

table td {
    border: 1px solid black;
}

table td {
    text-align: left;
    padding: 10px;
}

table *.invisible {
    border-color: transparent;
    width: 100px;
}

table button {
    width: 100% !important;
    margin: 0 !important;
}
</style>