<template>
  <div class="form">
    <div class="about">
      <h1>Генератор</h1>
    </div>
    <p>1. Выберите вариант загружаемого исходного файла:</p>
    <select id="type">
      <option value="1">XML</option>
      <option value="2">ZIP</option>
      <option value="3">DOCX</option>
    </select>
    <p>2. Выберите файл с устройства</p>
    <input type="file" id="file" ref="fileInput" @change="fileChange">
    <span v-if="errors.fileEmpty" class="error">Для начала надо выбрать файл для загрузки!</span>
    <p>3. Отправьте на сервер для обработки</p>
    <div>
      <button @click="send()" class="btn">Обработать</button>
    </div>
    <div class="ready" v-if="ready">
      <a :href="downloadLink" class="btn">Скачать готовый файл</a>
    </div>
  </div>
</template>

<script>
// import $ from 'jquery';

export default{
  name: 'GenerateView',
  data(){
    return{
      ready : false,
      downloadLink : '#',
      file : null,
      API : process.env.VUE_APP_API,
      errors: {
        fileEmpty : false,
      }
    }
  },
  methods:{
    send(){
      if(this.file == null){
        this.errors.fileEmpty = true;
      }else{
        this.errors.fileEmpty = false;
      }
      this.upload();
      console.log();
    },
    fileChange(){
      this.file = this.$refs.fileInput.files[0];
    },
    upload(){
      this.ready = false;
      /*
      $.ajax({
        url: this.API + 'upload/'
      }).done((data)=>{
        this.downloadLink = this.API + 'get?file=' + data.data.name;
        this.ready = true;
      });
      */
     console.log(this.API + 'upload/');
      fetch(this.API + 'upload/', {
        headers: {
          "Access-Control-Allow-Origin" : "*",
          "Origin" : this.API
        }
      }).then((data)=>{
        console.log(data);
        //this.downloadLink = this.API + 'get?file=' + data.data.name;
        //this.ready = true;
      });
    }
  },
}
</script>

<style>
#app .form, .ready{
  text-align: left;
  width: 720px;
  margin: 0 auto;
}
#app .form p{
  margin: 15px 0 5px 0;
}
#app a.btn {
  display: inline-block;
  padding: 15px;
  background-color: rgb(12, 44, 189);
  border-radius: 10px;
  color: white;
  text-decoration: none;
  margin-top: 20px;
}
#app span.error {
  color: red;
}
</style>